import { gsap } from "gsap";
import { lscroll } from "./scroll";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin";
import Highway from '@dogstudio/highway';
import { smooth } from "./easing";
import contact from "./contact";

class play_js extends Highway.Renderer {

    onEnter(){

        console.log("I am Play render");

        if (window.matchMedia("(max-width: 900px)").matches){
            window.scrollTo(0,0);
            console.log("mathc")
        }

        document.querySelectorAll(".js-year").forEach((elm)=>{elm.innerHTML ="" + new Date().getFullYear()});

        // lscroll.update();
        gsap.registerPlugin(ScrollTrigger);

        setTimeout(()=>{
            ScrollTrigger.refresh();
            console.log("called");
        }, 2000);


        //for enabling the contact form

        document.querySelector("#contact_btn").addEventListener("click", ()=>{
            contact.open();
            if(contact.first==true){
                setTimeout(()=>{contact.next_query();}, 1000);
                setTimeout(()=>{
                    contact.next_query();
                }, 4500);
                contact.first=false;
            } 
            console.log("i attached the even to play button")
        });

        //footer stuff
        

        ScrollTrigger.create({
            trigger: "footer",
            scroller: "#js-scroll",
            start: "top 50",
            onEnter: nav_white,
            onLeave: nav_blck,
            onEnterBack: nav_white,
            onLeaveBack: nav_blck
        });

        function nav_white(){
            document.querySelector("nav").classList.add("--white");
        }

        function nav_blck(){
            document.querySelector("nav").classList.remove("--white");
        }

        gsap.from("#c-circle", {
            scrollTrigger:{
                trigger: "footer",
                scroller: "#js-scroll",
                // start: "top top",
                scrub: true
            },
            y: "-70%",
            duration: 2,
            ease: smooth
        })

        if (window.matchMedia("(max-width: 900px)").matches){
            window.scrollTo(0,0);
            console.log("mathc")
        }
    }
  
    // onLeave(){
    // //   scroll.destroy();
    // }
}
  

export default play_js;